              <div class="col-sm-12 card shadow-sm border-0">
                <div class="card-body p-4">
                  <label class="pb-4 font-weight-bold">Would you like to share this app with others?</label>
                  <div class="d-flex justify-content-around">
                    <div class="custom-control custom-radio text-center">
