                        :disabled="disable"
                        id="s1"
                        value="1"
                      />
                      <label class="custom-control-label" for="s1">Yes</label>
                    </div>
                    <div class="custom-control custom-radio text-center">
                      <input
                        type="radio"
                        class="custom-control-input"
                        v-model="appShare"
                        :disabled="disable"
                        id="s2"
                        value="0"
                      />
                      <label class="custom-control-label" for="s2">No</label>
                    </div>
                  </div>
                  <span class="pt-4"></span>
                </div>
              </div>
            </div>
            <!-- referral -->
            <div class="row mt-4 mb-4">
              <div class="col-sm-12 card shadow-sm border-0">
                <div class="card-body p-4">
                  <label class="pb-4 font-weight-bold">How did you got to know about Fasdo?*</label>
                  <div class="justify-content-around">
                    <div class="custom-control custom-radio">
                      <input
                        type="radio"
                        class="custom-control-input"
                        v-model="referral"
                        :disabled="disable"
                        id="r1"
                        value="google"
                        required
                      />
                      <label class="custom-control-label" for="r1">Google</label>
                    </div>
                    <div class="custom-control custom-radio">
                      <input
                        type="radio"
                        class="custom-control-input"
                        v-model="referral"
                        :disabled="disable"
                        id="r2"
                        value="quora"
                      />
                      <label class="custom-control-label" for="r2">Quora</label>
                    </div>
                    <div class="custom-control custom-radio">
                      <input
                        type="radio"
                        class="custom-control-input"
                        v-model="referral"
                        :disabled="disable"
                        id="r3"
                        value="facebook"
                      />
                      <label class="custom-control-label" for="r3">Facebook</label>
