</template>

<script>
export default {
  name: "feedbackForm",
  data() {
    return {
      name: null,
      email: null,
      dob: null,
      deliveryLocation: null,
      productRating: null,
      valueForMoney: null,
      orderInTime: null,
      referral: null,
      appShare: null,
      orderAgain: null,
      improvements: null,
      image: null,
      submitted: false
    };
  },

  methods: {
    async save() {
      if (this.validate) {
        if (this.$refs.image.files[0] && this.$refs.image.files[0].name) {
          await this.uploadImage().then(downloadURL => {
            this.image = downloadURL;
          });
        }

        Api()
          .post("feedback", {
            name: this.name,
            email: this.email,
            dob: this.dob,
            deliveryLocation: this.deliveryLocation,
            productRating: this.productRating,
            valueForMoney: this.valueForMoney,
            orderInTime: this.orderInTime,
            referral: this.referral,
            appShare: this.appShare,
            orderAgain: this.orderAgain,
            improvements: this.improvements,
            image: this.image
          })
          .then(res => {
            if (res.status == 200 && res.data == "Success") {
              this.submitted = true;
              this.disable = false;
            }
          })
          .catch(err => {
            this.message = "Some error occured! Please try again later";
            this.success = "failed";
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          });
      }
    },
    uploadImage() {
      if (
        this.$refs.image.files[0] &&
        this.$refs.image.files[0].name != null &&
        this.$refs.image.files[0].name != undefined
      ) {
        let file = this.$refs.image.files[0];
        this.image = null;
        return firestorage
          .ref("feedback/" + file.name)
          .put(file)
          .then(res => {
            //downloading url
            let mediaURL = res.ref.getDownloadURL();
            return mediaURL;
          })
          .catch(err => {
            //// to show
            return null;
          });
      }
    },

    validate() {
      if (
        this.name &&
        this.contact &&
        this.deliveryLocation &&
        this.productRating &&
        this.valueForMoney &&
        (this.orderInTime == 0 || this.orderInTime == 1) &&
        this.referral &&
        this.appShare == 0 &&
        this.appShare == 1 &&
        (this.orderAgain == 0 || this.orderAgain == 1 || this.orderAgain == -1)
      ) {
        return true;
      } else {
        this.message = "Looks like you missed something. Please check";
        this.success = "failed";

        setTimeout(() => {
          this.success = false;
          this.message = null;
        }, 2000);
        return false;
      }
    }
  }
};
</script>

<style scoped>
.feedback {
  background-color: #e6f1f0;
}

.feedback-header-image {
  background-image: url("https://www.etsplc.com/wp-content/uploads/2017/10/feedback-value-leaders-banner.jpg");
  height: 15vh;
  top: 0px;
}
</style>



