<template>
  <div>
    <section class="jumbotron jumbotron-fluid section-1 app-header">
      <div class="container app-container">
        <div class="row">
          <div class="col-12 justify-content-center align-items-center text-center">
            <h3 class="font-weight-bolder">{{heading}}</h3>
          </div>
        </div>
      </div>
    </section>

    <section class="container app-container">
      <div class="row mb-4 policy">
        <div class="col-12">
          <div v-html="content"></div>
        </div>
      </div>
    </section>
    <LoaderGif :loading="disable" :responseText="success" :showMessage="message" />
  </div>
</template>

<script>
import Api from "@/services/Api.js";

export default {
  name: "policyDetail",

  data() {
    return {
      heading: null,
      content: null
    };
  },

  beforeRouteEnter(to, from, next) {
    Api()
      .get("aboutUs", {
        params: {
          name: to.params.name
        }
      })
      .then(res => {
        res.data.forEach(doc => {
          next(vm => {
            if (to.params.name == "termsandcondition") {
              vm.heading = "Terms and Conditions";
            } else if (to.params.name == "privacypolicy") {
              vm.heading = "Privacy Policy";
            } else if (to.params.name == "aboutus") {
              vm.heading = "About US";
            } else if (to.params.name == "applicense") {
              vm.heading = "License";
            }
            vm.content = doc;
          });
        });
      })
      .catch(err => {
        console.log(err.message);
      });
  },

  watch: {
    $route: "fetchContent"
  },

  methods: {
    fetchContent() {
      this.disable = true;
      if (this.$route.params.name == "termsandcondition") {
        this.heading = "Terms and Conditions";
      } else if (this.$route.params.name == "privacypolicy") {
        this.heading = "Privacy Policy";
      } else if (this.$route.params.name == "returnpolicy") {
        this.heading = "Return and Refund Policy";
      } else if (this.$route.params.name == "aboutus") {
        this.heading = "About US";
      } else if (this.$route.params.name == "applicense") {
        this.heading = "License";
      }

      Api()
        .get("aboutUs", {
          params: {
            name: this.$route.params.name
          }
        })
        .then(res => {
          res.data.forEach(doc => {
            this.content = doc;
            this.disable = false;
          });
        })
        .catch(err => {
          this.disable = false;
        });
    }
  }
};
</script>

<style scoped>
.policy {
  height: 60vh;
  overflow: auto;
}

.aboutUs h1,
.aboutUs h2,
.aboutUs h3,
.aboutUs h4,
.aboutUs h5,
.aboutUs h6 {
  font-size: 1.1 rem;
  font-weight: normal;
  color: #2c211e;
}

.aboutUs strong {
  color: #2c211e;
}

.aboutUs {
  font-family: "Poppins";
  font-size: 1 rem;
  line-height: 1 em;
  color: #606c71;
}
</style>



