<template>
  <div>
    <!-- location -->
    <section class="jumbotron jumbotron-fluid section-1 app-header">
      <div class="container app-container">
        <div class="row">
          <div class="col-12 justify-content-center align-items-center text-center">
            <h4 class="text-capitalize">Where do you want us to deliver?</h4>
            <div>
              <b>Grocery , Personal Care or Daily Needs ? Fasdo Kro and We're there in 90 mins</b>
            </div>
          </div>
        </div>
        <div class="row">
          <div class="col-12">
            <form class="form-inline mt-4 pt-4 mb-4 d-flex justify-content-center">
              <div class="input-group mb-3">
                <div class="input-group-prepend">
                  <span class="input-group-text bg-white border-0" id="basic-addon1">
                    <i class="fa fa-map-marker fa-lg" aria-hidden="true"></i>
                  </span>
                </div>
                <input
                  class="form-control form-control-sm f-form-control border-0 rounded-right"
                  type="text"
                  v-model="location"
                  @focus="locationFound=false"
                  placeholder="Search for Location"
                  aria-label="Search"
                  aria-describedby="Search Location"
                />
                <button
                  class="btn btn-app-theme ml-2"
                  type="submit"
                  :disabled="!location || location.length<4"
                  @click.prevent="getLocation()"
                >
                  <span class="pl-4 pr-4" v-if="!loadingLoc">SEARCH</span>
                  <span
                    class="spinner-border spinner-border-sm text-secondary ml-4 mr-4"
                    role="status"
                    v-else
                  ></span>
                </button>
              </div>
            </form>
          </div>
        </div>
        <!-- order now -->
        <div class="row" v-if="locationFound">
          <div class="col-12 text-center">
            <router-link to="/order" class="btn btn-app-theme ml-2" tag="button">
              <span class="pl-4 pr-4">ORDER NOW</span>
            </router-link>
          </div>
        </div>
      </div>
    </section>

    <!-- categories -->
    <section class="app-container">
      <div class="row">
        <div class="col-12">
          <h5 class="text-capitalize">What do you want us to deliver?</h5>
          <hr class="home-hr float-left" />
        </div>
      </div>
      <div class="row">
        <div class="col-12">
          <div class="card shadow-sm border-app-secondary">
            <div class="row card-body" v-if="!loadingCat">
              <div
                class="card border-0 category col-4 col-md-3 align-self-center text-center"
                v-for="category in categories"
                :key="category.subcatcode"
              >
                <img
                  class="card-img-top align-self-center"
                  :src="category.image"
                  :alt="category.name"
                />
                <div class="card-body border-0 pl-0 pr-0">
                  <b>{{category.name}}</b>
                </div>
              </div>
            </div>
            <div class="row card-body align-self-center m-4" v-else>
              <div class="spinner-grow" style="width: 5rem; height: 5rem;" role="status">
                <span class="sr-only">Loading...</span>
              </div>
            </div>
            <div class="card-footer section-3">
              <div class="col-12 justify-content-center align-items-center text-center">
                <a :href="appLink" class="btn pt-3 pb-3 pl-4 pr-4 text-decoration-none">
                  <span class="font-weight-bold">DOWNLOAD THE APP</span>
                  <img
                    src="@/assets/img/icons/getAppColor.svg"
                    class="img-fluid align-self-center pl-2"
                    alt="get app"
                    width="20"
                  />
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>

    <!-- services -->
    <section class="section-4">
      <div class="app-container">
        <div class="row">
          <div class="col-12">
            <h5 class="text-capitalize">Fasdo means Fast & Quick</h5>
            <hr class="home-hr float-left" />
          </div>
        </div>
        <div class="row card-deck">
          <div class="col-sm-6 card border-app-secondary">
            <div class="card-body p-4">
              <img
                class="section-service-image"
                src="@/assets/img/clock.svg"
                alt="delivery in 90 mins"
              />
              <h5 class="text-capitalize card-title font-weight-bold">Delivery In 90 Minutes</h5>
              <b class="card-text">The real time saver. Get it delivered lightning quick.</b>
            </div>
          </div>
          <div class="col-sm-6 card border-app-secondary">
            <div class="card-body p-4">
              <img
                class="section-service-image"
                src="@/assets/img/return.svg"
                alt="delivery in 90 mins"
              />
              <h5 class="text-capitalize card-title font-weight-bold">Hassle Free Returns</h5>
              <b
                class="card-text"
              >No need to worry about the returns. One Click and your items can be returned</b>
            </div>
          </div>
          <div class="col-sm-6 card border-app-secondary">
            <div class="card-body p-4">
              <img
                class="section-service-image"
                src="@/assets/img/deliveryTruck.svg"
                alt="delivery in 90 mins"
              />
              <h5 class="text-capitalize card-title font-weight-bold">All day delivery</h5>
              <b
                class="card-text"
              >Don’t wait for weeks to get your order. Get it delivered to you in minutes</b>
            </div>
          </div>
        </div>
      </div>
    </section>

    <!-- download button -->
    <section class="jumbotron jumbotron-fluid section-5 bg-dark">
      <div class="container app-container">
        <div class="row">
          <div class="col-12 justify-content-center align-items-center text-center">
            <a :href="appLink" class="btn pt-3 pb-3 pl-4 pr-4 text-decoration-none">
              <img
                src="@/assets/img/googlePlayLight.svg"
                class="img-fluid align-self-center pl-2"
                alt="get app"
                width="170"
              />
            </a>
          </div>
        </div>
        <div class="row">
          <div class="col-12">
            <form class="form-inline mt-4 pt-4 mb-4 d-flex justify-content-center">
              <GetAppLink />
            </form>
          </div>
        </div>
      </div>
    </section>

    <!-- testimonials -->
    <section class="section-6">
      <div class="app-container">
        <div class="row">
          <div class="col-12">
            <h5 class="text-capitalize">
              Why People
              <span class="text-color-app-theme">Use Fasdo</span>
            </h5>
            <hr class="home-hr float-left" />
          </div>
        </div>
        <div
          class="row flex-row flex-nowrap overflow-auto"
          v-if="testimonials.length>0 && !loadingProof"
        >
          <div
            class="col-9 col-md-5 card border-app-secondary testimonial"
            v-for="(feedback,index) in testimonials"
            :key="index+1"
          >
            <div class="card-body p-4 pl-2 pb-0">
              <!--
              <img
                class="testimonial-image img-circle"
                :src="feedback.image"
                alt="testimonial image"
                v-if="feedback.image"
              />
              <img
                class="testimonial-image img-circle"
                src="@/assets/img/deliveryTruck.svg"
                alt="testimonial image"
                v-else
              />
              -->
              <img width="60px" src="@/assets/img/icons/horizontal-dot.svg" alt="testimonial" />
              <p class="testimonial">
                <small v-if="!feedback.viewComplete">{{feedback.feedback.substring(0, 180)}}</small>
                <small v-else>{{feedback.feedback}}</small>
                <small
                  v-if="feedback.feedback.length>180 && !feedback.viewComplete"
                  class="text-info cursor-pointer"
                  @click.prevent="feedback.viewComplete=true"
                >...Read more</small>
                <small
                  v-if="feedback.feedback.length>180 && feedback.viewComplete"
                  class="text-info cursor-pointer"
                  @click.prevent="feedback.viewComplete=false"
                >...Show less</small>
              </p>
            </div>
            <small
              class="card-footer bg-transparent font-weight-semibold border-top-0 mb-4"
            >{{feedback.name}}</small>
          </div>
        </div>
        <div class="align-self-center text-center m-4" v-else>
          <div class="spinner-grow" style="width: 5rem; height: 5rem;" role="status">
            <span class="sr-only">Loading...</span>
          </div>
        </div>
      </div>
    </section>
    <LoaderGif :loading="disable" :responseText="success" :showMessage="message" />
  </div>
</template>

<script>
import Api from "@/services/Api.js";
import GetAppLink from "@/components/GetAppLink.vue";

export default {
  name: "landingPage",
  components: {
    GetAppLink
  },

  data() {
    return {
      categories: [],
      loadingCat: false,
      loadingLoc: false,
      loadingProof: false,
      location: null,
      locationFound: false,
      testimonials: []
    };
  },

  created() {
    this.getCategories();
    this.getTestimonials();
  },

  methods: {
    click() {
      this.$ga.event({
        eventCategory: "DownloadButton",
        eventAction: "Pressed",
        eventValue: 1
      });
    },

    getCategories() {
      this.categories = [];
      this.loadingCat = true;
      Api()
        .get("cat", {
          params: {
            active: 1,
            order: "priority",
            orderWay: "ASC"
          }
        })
        .then(res => {
          res.data.forEach(doc => {
            let cat = {
              id: doc.subcatcode,
              name: doc.name,
              image: doc.image
            };
            this.categories.push(cat);
          });
          this.loadingCat = false;
        })
        .catch(err => {
          this.loadingCat = false;
        });
    },

    getLocation() {
      this.loadingLoc = true;
      this.locationFound = false;
      Api()
        .get("delivery/location/check", {
          params: {
            location: this.location
          }
        })
        .then(res => {
          if (res.status == 200 && res.data >= 0) {
            if (res.data > 0) {
              this.message = "Horray! We deliver at your location";
              this.locationFound = true;
            } else {
              this.message =
                "Oops! looks like we didn't reach your location yet or Try entering more fine location";
            }
            this.success = true;
            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 5000);
          }
          this.loadingLoc = false;
        })
        .catch(err => {
          this.message =
            "Oops! some error occured while searching. Please try again later.";

          this.loadingLoc = false;
          this.success = true;
          setTimeout(() => {
            this.success = false;
            this.message = null;
          }, 5000);
        });
    },

    getTestimonials() {
      this.testimonials = [];
      this.loadingProof = true;
      Api()
        .get("home/testimonials")
        .then(res => {
          res.data.forEach(feedback => {
            feedback["viewComplete"] = false;
            this.testimonials.push(feedback);
          });
          this.loadingProof = false;
        })
        .catch(err => {
          this.loadingProof = false;
        });
    }
  }
};
</script>

<style scoped>
.category img {
  width: 5em;
}

.section-1 button {
  background: #402b16;
}

.section-1 button:disabled {
  background-color: rgba(255, 255, 204, 0.3);
  opacity: 100%;
}

.section-3 {
  background: #fff8f3;
}

.section-3 a {
  border: 1.5px dashed #ff6a02;
  background: #ffe1cc;
  color: #ff6a02;
}

.section-3 a:hover {
  color: #ff6a02;
}

.home-hr {
  border: 1px dashed;
  width: 14.5rem;
  margin-top: 0.5rem;
}

.section-service-image {
  height: 7em;
  position: absolute;
  top: -75px;
  border-top-left-radius: calc(0.25rem - 1px);
  border-top-right-radius: calc(0.25rem - 1px);
}

.section-6 .card {
  margin-top: 70px;
  margin-right: 55px;
}

.section-6 .testimonial-image {
  height: 7em;
  position: absolute;
  top: -55px;
  right: -55px;
  border-top-left-radius: calc(0.25rem - 1px);
  border-top-right-radius: calc(0.25rem - 1px);
}

.section-6 .testimonial {
  margin-top: 25px;
}

/* Hide scrollbar for Chrome, Safari and Opera */
.section-6 .row::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE and Edge */
.section-6 .row {
  -ms-overflow-style: none;
}

.section-4 .card .card-body {
  margin-top: 35px;
}

.section-4 .card {
  margin-top: 90px;
}
</style>



