<template>
  <div class="input-group mb-3 applink">
    <div class="input-group-prepend">
      <span class="input-group-text bg-white border-right-0" id="mobile">
        <i class="fa fa-mobile fa-lg" aria-hidden="true"></i>
      </span>
    </div>
    <input
      class="form-control form-control-sm border-left-0 rounded-right f-form-control"
      type="number"
      v-model="mobileNumber"
      placeholder="Enter Mobile Number"
      aria-label="MoobileNumber"
      aria-describedby="Enter Mobile Number"
    />
    <button
      class="btn btn-app-theme ml-2"
      type="submit"
      :disabled="!mobileNumber || mobileNumber.length!=10"
      @click.prevent="sendAppLink"
    >
      <span class="pl-3 pr-3" v-if="!sendingAppLink">GET APP LINK</span>
      <span class="spinner-border spinner-border-sm text-secondary ml-4 mr-4" role="status" v-else></span>
    </button>
  </div>
</template>

<script>
import Api from "@/services/Api.js";

export default {
  name: "appLink",
  data() {
    return {
      sendingAppLink: false,
      mobileNumber: null
    };
  },
  methods: {
    sendAppLink() {
      this.sendingAppLink = true;
      Api()
        .post("app/request", {
          phoneNumber: this.mobileNumber
        })
        .then(res => {
          this.sendingAppLink = false;
          if (res.data == "Success" && res.status == 200) {
            this.mobileNumber = null;
            this.message =
              "Thankyou for trying Fasdo. App link has been sent on your number.";
            this.success = true;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 5000);
          }
        })
        .catch(err => {
          this.sendingAppLink = false;
          this.message =
            "Some error occured while sending you app link. Please try again later.";
          this.success = true;

          setTimeout(() => {
            this.success = false;
            this.message = null;
          }, 5000);
        });
    }
  }
};
</script>

<style scoped>
.applink button:disabled {
  background-color: #fff3eb;
  color: rgba(0, 0, 0, 0.4);
  opacity: 100%;
}
</style>



