<template>
  <div>
    <section>
      <div class="app-container">
        <div class="row justify-content-center align-items-center pt-4">
          <div class="col-sm-12">
            <form class="contact-form">
              <!-- delivery location -->
              <div class="form-row">
                <div class="col-md-12 mb-3">
                  <input
                    type="text"
                    class="form-control form-control-sm order-form-control f-form-control"
                    :value="'Delivery to '+this.landmark"
                    disabled="true"
                    required
                  />
                </div>
              </div>
              <!-- name -->
              <div class="form-row">
                <div class="col-md-12 mt-3 mb-3">
                  <label class="font-weight-semibold order-label">Your Name</label>
                  <input
                    type="text"
                    class="form-control form-control-sm order-form-control f-form-control"
                    v-model="name"
                    :disabled="disable"
                    required
                  />
                  <span class="text-danger" v-if="errors[1]">{{ errors[1] }}</span>
                </div>
              </div>
              <!-- item list -->
              <div class="form-row">
                <div class="col-md-12 mt-3 mb-3">
                  <label class="font-weight-semibold order-label">Make a List of items</label>
                  <textarea
                    class="form-control form-control-sm order-form-control f-form-control"
                    rows="12"
                    placeholder="Format: 1. Item Name - Brand - Quantity"
                    :disabled="disable"
                    required
                    v-model="listItems"
                  ></textarea>
                  <span class="text-danger" v-if="errors[2]">{{ errors[2] }}</span>
                </div>
              </div>
              <!-- number -->
              <div class="form-row">
                <div class="col-md-12 mt-3 mb-3">
                  <label class="font-weight-semibold order-label">Your Mobile Number</label>
                  <input
                    type="text"
                    class="form-control form-control-sm order-form-control f-form-control"
                    placeholder="Enter 10-digit number(eg: 98XXXXXXXX)"
                    v-model="mobileNumber"
                    maxlength="10"
                    :disabled="disable"
                    required
                  />
                  <span class="text-danger" v-if="errors[3]">{{ errors[3] }}</span>
                </div>
              </div>
              <!-- delivery address -->
              <div class="form-row">
                <div class="col-md-12 mt-3 mb-3">
                  <label class="font-weight-semibold order-label">Delivery Address</label>
                  <!-- delivery number number -->
                  <input
                    type="text"
                    class="form-control form-control-sm order-form-control f-form-control"
                    placeholder="Enter 10-digit number(eg: 98XXXXXXXX)"
                    v-model="deliveryMobileNumber"
                    :disabled="disable"
                    required
                  />
                  <!-- house number -->
                  <input
                    type="text"
                    class="form-control form-control-sm order-form-control f-form-control mt-4"
                    placeholder="Flat No/ House No/ Road No/Area/Locality"
                    v-model="houseNumber"
                    :disabled="disable"
                    required
                  />
                  <!-- pincode -->
                  <input
                    type="text"
                    class="form-control form-control-sm order-form-control f-form-control mt-4"
                    placeholder="Pincode(ex: 831XXX)"
                    v-model="pincode"
                    :disabled="disable"
                    required
                  />
                  <span class="text-danger" v-if="errors[4]">{{ errors[4] }}</span>
                </div>
              </div>

              <!-- any instructions -->
              <div class="form-row">
                <div class="col-md-12 mt-3 mb-3">
                  <label class="font-weight-semibold order-label">Add Any Instructions</label>
                  <input
                    type="text"
                    class="form-control form-control-sm order-form-control f-form-control"
                    placeholder="ex: don't deliver today"
                    v-model="comments"
                    :disabled="disable"
                    required
                  />
                  <span class="text-danger" v-if="errors[1]">{{ errors[1] }}</span>
                </div>
              </div>

              <div class="form-row">
                <div class="col-md-12 mt-3 mb-4" v-if="!otpSent">
                  <button
                    type="submit"
                    class="btn btn-submit pt-3 pb-3 btn-fasdo-primary"
                    :disabled="!mobileNumber || (mobileNumber && mobileNumber.length!=10)"
                    @click.prevent="sendOTP()"
                  >Get OTP</button>
                </div>
                <div class="col-md-12 mt-3 mb-4" v-if="otpSent && !otpVerified">
                  <!-- otp -->
                  <div class="form-row w-25">
                    <div class="col-md-12 mt-3 mb-3">
                      <label class="font-weight-semibold order-label">Enter OTP</label>
                      <input
                        type="text"
                        class="form-control form-control-sm order-form-control f-form-control"
                        placeholder="XXXX"
                        v-model="otp"
                        maxlength="10"
                        :disabled="disable"
                        required
                      />
                      <span class="text-danger" v-if="errors[1]">{{ errors[1] }}</span>
                    </div>
                  </div>
                  <button
                    type="submit"
                    class="btn btn-submit pt-3 pb-3 btn-fasdo-primary"
                    :disabled="!otp || (otp && otp.length>4)"
                    @click.prevent="verifyOTP()"
                  >Verify OTP</button>
                </div>
                <div class="col-md-12 mt-3 mb-4" v-if="otpSent && otpVerified">
                  <span
                    class="text-danger pt-2"
                    v-if="errors[0] || errors[1] || errors[2]"
                  >Looks like you missed something. Please Check</span>
                  <br v-if="errors[0] || errors[1] || errors[2]" />
                  <br />
                  <button
                    type="submit"
                    class="btn btn-submit pt-3 pb-3 btn-fasdo-primary"
                    @click.prevent="save()"
                  >Confirm Order</button>

                  <div class="font-weight-semibold tca mt-4">
                    By confirming order you agree to Fasdo Terms of Service. You agree that list
                    doesn’t not conatin any restricted or illegal items. If such item is found
                    we will cancel the order.
                  </div>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </section>
    <LoaderGif :loading="disable" :responseText="success" :showMessage="message" />
  </div>
</template>

<script>
import Api from "@/services/Api.js";
import Vue from "vue";
export default {
  name: "OrderRequest",

  data() {
    return {
      landmark: "bistupur",
      name: null,
      listItems: null,
      mobileNumber: null,
      houseNumber: null,
      deliveryMobileNumber: null,
      pincode: null,
      comments: null,
      errors: [],
      otpVerified: false,
      otpSent: false,
      otp: null
    };
  },

  methods: {
    save() {
      if (this.validate()) {
        this.disable = true;
        Api()
          .post("order/request", {
            name: this.name,
            mobileNumber: this.mobileNumber,
            itemsList: this.listItems,
            deliveryMobileNumber: this.deliveryMobileNumber,
            houseOfficeNo: this.houseNumber,
            landmark: this.landmark,
            pincode: this.pincode,
            comments: this.comments,
            paymentMode: 1
          })
          .then(res => {
            if (res.data == "Success") {
              //// to show

              this.message =
                "Thankyou for placing order. Our customer excetive will get back to you once your order is processed.";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 5000);
            }
          })
          .catch(err => {
            this.message =
              "Sorry! Looks like some error has occured. Please Try again later or contact our customer care";
            this.success = "failed";
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 5000);
          });
      }
    },

    validate() {
      this.errors = [];
      if (
        this.name &&
        this.listItems &&
        this.mobileNumber &&
        this.houseNumber &&
        this.deliveryMobileNumber &&
        this.landmark &&
        this.otpSent &&
        this.otpVerified
      ) {
        return true;
      } else {
        Vue.set(this.errors, 0, "You missed something");
        if (!this.name) {
          Vue.set(this.errors, 1, "Please enter your name");
        }
        if (!this.listItems) {
          Vue.set(this.errors, 2, "Please make your list");
        }
        if (!this.mobileNumber) {
          Vue.set(this.errors, 3, "Please enter your mobile number");
        }
        if (!this.deliveryMobileNumber || this.houseNumber || this.pincode) {
          Vue.set(this.errors, 4, "Please complete your address detail");
        }
        if (!this.otpSent || !this.otpVerified) {
          Vue.set(this.errors, 0, "Number not verified");
        }
        return false;
      }
    },

    sendOTP() {
      this.errors[0] = null;
      this.otpSent = false;
      if (this.mobileNumber.length == 10) {
        Api()
          .post("user/send/otp", {
            mobileNumber: this.mobileNumber
          })
          .then(res => {
            if (res.data == "Success") {
              //// to show
              this.otpSent = true;

              this.message =
                "OTP has been send to your mobile number. Please check";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 5000);
            }
          })
          .catch(err => {
            this.message =
              "Sorry! Looks like some error has occured. Please Try again later or contact our customer care";
            this.success = "failed";
            this.disable = false;
            this.otpSent = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 5000);
          });
      } else {
        Vue.set(
          this.errors,
          0,
          "Please enter your number. It needs to be verified first."
        );
      }
    },

    verifyOTP() {
      this.otpVerified = false;
      if (this.mobileNumber.length == 10) {
        Api()
          .post("user/verify/otp", {
            _method: "PUT",
            mobileNumber: this.mobileNumber,
            otp: this.otp
          })
          .then(res => {
            if (res.data.length > 0) {
              if (
                res.data == "Failed" ||
                res.data == "Already Verified" ||
                res.data == "Invalid" ||
                res.data == "Number Not Valid"
              ) {
                this.message = res.data;
              } else if (res.data.length >= 10) {
                this.message = "Thankyou! Your number has been verified";
                this.otpVerified = true;
              } else {
                this.message =
                  "Sorry! Looks like some error has occured. Please Try again later or contact our customer care";
              }
              //// to show

              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 5000);
            }
          })
          .catch(err => {
            this.message =
              "Sorry! Looks like some error has occured. Please Try again later or contact our customer care";
            this.success = "failed";
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 5000);
          });
      } else {
        Vue.set(
          this.errors,
          0,
          "Please enter your number. It needs to be verified first."
        );
      }
    }
  }
};
</script>

<style scoped>
.home-hr {
  border: 1px dashed;
  width: 15.5rem;
  margin-top: 0;
}

.order-form-control {
  border: 1.5px dashed #c1c1c1;
  background-color: #f4f4f4;
}

.order-label {
  color: #20092d;
}

.tca {
  font-size: small;
  color: #7f7f7f;
  max-width: 40em;
}

.btn-submit {
  padding-left: 4em;
  padding-right: 4em;
}
</style>



