import Vue from 'vue'
import Router from 'vue-router'
// about us
import appLicense from '@/components/policies/appLicense'
import contactUs from '@/components/views/contactUs'
import faq from '@/components/views/faq'
import feedback from '@/components/views/FeedbackForm'
import landing from '@/components/views/landing'
import logo from '@/components/logo'
import policyDetail from '@/components/policies/policy'
import orderRequest from '@/components/views/orderRequest.vue'

Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [
        // aboutUs
        {
            path: '/policy/:name',
            name: 'policy',
            component: policyDetail,
            meta: {
                title: 'FasDo Policy'
            }
        },
        // aboutUs
        {
            path: '/app/license',
            name: 'applicense',
            component: appLicense,
            meta: {
                title: 'License'
            }
        },
        // contactUs
        {
            path: '/contact',
            name: 'contactUs',
            component: contactUs,
            meta: {
                title: 'Contact Us'
            }
        },
        // faq
        {
            path: '/faq',
            name: 'faq',
            component: faq,
            meta: {
                title: 'FAQ'
            }
        },
        // feedback
        {
            path: '/feedback',
            name: 'feedback',
            component: feedback,
            meta: {
                title: 'Feedback Form'
            }
        },
        // order request
        {
            path: '/order',
            name: 'orderRequest',
            component: orderRequest,
            meta: {
                title: 'Order'
            }
        },
        // landing
        {
            path: '/',
            name: 'landing',
            component: landing,
            meta: {
                title: 'FasDo'
            }
        },
        // logo
        {
            path: '/logo',
            name: 'logo',
            component: logo,
            meta: {
                title: 'FasDo'
            },
        }
    ]
})


