<template>
  <div>
    <section class="jumbotron jumbotron-fluid section-1 app-header">
      <div class="container app-container">
        <div class="row">
          <div class="col-12 justify-content-center align-items-center text-center">
            <h3 class="font-weight-bolder">Contact Us</h3>
            <span>Have a Question?</span>
          </div>
        </div>
      </div>
    </section>

    <section>
      <div class="app-container">
        <div class="row">
          <div class="col-12">
            <h4 class="text-capitalize">Get in touch with us</h4>
            <hr class="home-hr float-left" />
          </div>
        </div>
        <div class="row justify-content-center align-items-center pt-4">
          <div class="col-sm-12">
            <form class="contact-form">
              <!-- name -->
              <div class="form-row">
                <div class="col-md-12 mb-3">
                  <input
                    type="text"
                    class="form-control form-control-sm"
                    placeholder="Your Name"
                    v-model="name"
                    required
                  />
                  <span class="text-danger" v-if="errors[0]">{{ errors[0] }}</span>
                </div>
              </div>
              <!-- email -->
              <div class="form-row">
                <div class="col-md-12 mt-3 mb-3">
                  <input
                    type="text"
                    class="form-control form-control-sm"
                    placeholder="Your e-mail"
                    v-model="email"
                    required
                  />
                  <span class="text-danger" v-if="errors[1]">{{ errors[1] }}</span>
                </div>
              </div>
              <!-- subject -->
              <div class="form-row">
                <div class="col-md-12 mt-3 mb-3">
                  <input
                    type="text"
                    class="form-control form-control-sm"
                    v-model="subject"
                    placeholder="Subject"
                    required
                  />
                  <span class="text-danger" v-if="errors[3]">{{ errors[3] }}</span>
                </div>
              </div>
              <!-- message -->
              <div class="form-row">
                <div class="col-md-12 mt-3 mb-3">
                  <textarea
                    class="form-control form-control-sm"
                    rows="12"
                    v-model="query"
                    placeholder="Your message"
                  ></textarea>
                  <span class="text-danger" v-if="errors[2]">{{ errors[2] }}</span>
                </div>
              </div>
              <div class="form-row">
                <div class="col-6 mt-3 mb-3">
                  <div class="custom-control custom-radio custom-control-inline">
                    <input
                      type="radio"
                      class="custom-control-input f-form-control-radio"
                      id="feedback"
                      v-model="type"
                      value="1"
                    />
                    <label
                      class="custom-control-label font-weight-bold"
                      for="feedback"
                    >Is it a feedback?</label>
                  </div>
                </div>
                <div class="col-6 mt-3 mb-3">
                  <div class="custom-control custom-radio custom-control-inline">
                    <input
                      type="radio"
                      class="custom-control-input"
                      id="complaint"
                      v-model="type"
                      value="2"
                    />
                    <label
                      class="custom-control-label font-weight-bold"
                      for="complaint"
                    >Is it a complaint?</label>
                  </div>
                </div>
              </div>
              <div class="form-row">
                <div class="col-md-12 mt-3 mb-4 text-center">
                  <span
                    class="text-danger pt-2"
                    v-if="errors[0] || errors[1] || errors[2]"
                  >Looks like you missed something. Please Check</span>
                  <br v-if="errors[0] || errors[1] || errors[2]" />
                  <br />
                  <button
                    type="submit"
                    class="btn btn-submit btn-block btn-fasdo-primary"
                    @click.prevent="save()"
                  >SUBMIT</button>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </section>

    <!-- download button -->
    <section class="jumbotron jumbotron-fluid section-3">
      <div class="container app-container">
        <div class="row">
          <div class="col-12 justify-content-center align-items-center text-center">
            <a :href="appLink" class="btn pt-3 pb-3 pl-4 pr-4 text-decoration-none">
              <span class="font-weight-bold">DOWNLOAD THE APP</span>
              <img
                src="@/assets/img/icons/getAppColor.svg"
                class="img-fluid align-self-center pl-2"
                alt="get app"
                width="20"
              />
            </a>
          </div>
        </div>
      </div>
    </section>

    <section>
      <div class="app-container">
        <div class="row">
          <div clas="col-12"></div>
        </div>
      </div>
    </section>
    <LoaderGif :loading="disable" :responseText="success" :showMessage="message" />
  </div>
</template>

<script>
import Api from "@/services/Api.js";
import Vue from "Vue";

export default {
  name: "ContactUs",

  data() {
    return {
      name: null,
      email: null,
      subject: null,
      query: null,
      type: 1,
      errors: [null, null, null]
    };
  },
  methods: {
    save() {
      if (this.validate()) {
        this.disable = true;
        Api()
          .post("contact/query", {
            name: this.name,
            email: this.email,
            subject: this.subject,
            query: this.query,
            type: this.type
          })
          .then(res => {
            if (res.data == "Success") {
              //// to show

              this.message =
                "Thankyou for taking out your time.Your Query has been submitted. We'll be back soon!";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 5000);
            }
          })
          .catch(err => {
            this.message =
              "Sorry! Looks like some error has occured. Please Try again later";
            this.success = true;
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 5000);
          });
      }
    },

    validate() {
      this.errors = [null, null, null];
      if (
        this.name &&
        this.email &&
        this.subject &&
        this.subject.length <= 200 &&
        this.query &&
        /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(this.email) &&
        /^[a-zA-Z^\s]+$/.test(this.name)
      ) {
        return true;
      } else {
        if (!this.name) {
          Vue.set(this.errors, 0, "Please enter name");
        } else if (/^[a-zA-Z^\s]+$/.test(this.name) == false) {
          Vue.set(this.errors, 0, "Please enter valid name");
        }

        if (!this.email) {
          Vue.set(this.errors, 1, "Please enter email");
        } else if (
          /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(this.email) ==
          false
        ) {
          Vue.set(this.errors, 1, "Please enter valid email");
        }

        if (!this.subject) {
          Vue.set(this.errors, 3, "Please enter subject");
        } else if (this.subject.lenght > 200) {
          Vue.set(this.errors, 3, "Please write subject in 200 words");
        }

        if (!this.query) {
          Vue.set(this.errors, 2, "Please write down some query");
        }
        return false;
      }
    }
  }
};
</script>

<style scoped>
.home-hr {
  border: 1px dashed;
  width: 15.5rem;
  margin-top: 0;
}
</style>



